"use client"

import { useQuery } from "@tanstack/react-query"
import { 
  UserCheck, 
  Truck, 
  Package, 
  TrendingUp,
  AlertCircle,
  Clock,
  CheckCircle,
  ArrowRight
} from "lucide-react"
import Link from "next/link"
import { Container, PageHeader, Section } from "@/components/layout"
import { Card, CardContent, CardHeader, CardTitle, Badge, Skeleton, Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui"
import { formatPaise } from "@/lib/constants"
import { formatDateTime } from "@/lib/utils"

function DashboardSkeleton() {
  return (
    <div className="p-6 space-y-6">
      <Skeleton className="h-8 w-48" />
      <div className="grid grid-cols-2 lg:grid-cols-4 gap-4">
        {[1, 2, 3, 4].map(i => <Skeleton key={i} className="h-28" />)}
      </div>
      <div className="grid lg:grid-cols-2 gap-6">
        <Skeleton className="h-64" />
        <Skeleton className="h-64" />
      </div>
    </div>
  )
}

export default function AdminDashboard() {
  const { data, isLoading, error } = useQuery({
    queryKey: ["admin-dashboard"],
    queryFn: async () => {
      const res = await fetch("/api/admin/dashboard")
      if (!res.ok) throw new Error("Failed to fetch")
      return res.json()
    },
    refetchInterval: 60000,
  })

  if (isLoading) return <DashboardSkeleton />

  if (error) {
    return (
      <div className="p-6">
        <Card className="border-red-200 bg-red-50">
          <CardContent className="flex items-center gap-3 py-4 text-red-700">
            <AlertCircle className="w-5 h-5" />
            <p>Failed to load dashboard. Please refresh the page.</p>
          </CardContent>
        </Card>
      </div>
    )
  }

  const { overview, todayStats, inventory, todayRevenue, recentTransactions } = data.data

  return (
    <div className="p-4 lg:p-6">
      <Container size="xl" className="px-0">
        <Section spacing="sm">
          <PageHeader
            title="Dashboard"
            subtitle="Welcome back! Here's what's happening today."
          />
        </Section>

        {/* Pending Approvals Alert */}
        {overview.pendingApprovals > 0 && (
          <Section spacing="sm">
            <Link href="/admin/customers?status=PENDING_APPROVAL">
              <Card className="border-amber-200 bg-amber-50 hover:bg-amber-100 transition-colors cursor-pointer">
                <CardContent className="flex items-center justify-between py-4">
                  <div className="flex items-center gap-3">
                    <div className="w-10 h-10 bg-amber-200 rounded-lg flex items-center justify-center">
                      <Clock className="w-5 h-5 text-amber-700" />
                    </div>
                    <div>
                      <p className="font-semibold text-amber-800">
                        {overview.pendingApprovals} Pending Approval{overview.pendingApprovals > 1 ? 's' : ''}
                      </p>
                      <p className="text-sm text-amber-600">New customers waiting for your review</p>
                    </div>
                  </div>
                  <div className="flex items-center gap-2">
                    <Badge variant="warning">{overview.pendingApprovals}</Badge>
                    <ArrowRight className="w-4 h-4 text-amber-600" />
                  </div>
                </CardContent>
              </Card>
            </Link>
          </Section>
        )}

        {/* KPI Cards */}
        <Section spacing="sm">
          <div className="grid grid-cols-2 lg:grid-cols-4 gap-4">
            <Card>
              <CardContent className="p-4">
                <div className="flex items-center gap-3">
                  <div className="w-12 h-12 bg-emerald-100 rounded-xl flex items-center justify-center flex-shrink-0">
                    <UserCheck className="w-6 h-6 text-emerald-600" />
                  </div>
                  <div className="min-w-0">
                    <p className="text-2xl font-bold">{overview.activeCustomers}</p>
                    <p className="text-sm text-[hsl(var(--muted-foreground))] truncate">Active Customers</p>
                  </div>
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardContent className="p-4">
                <div className="flex items-center gap-3">
                  <div className="w-12 h-12 bg-blue-100 rounded-xl flex items-center justify-center flex-shrink-0">
                    <Truck className="w-6 h-6 text-blue-600" />
                  </div>
                  <div className="min-w-0">
                    <p className="text-2xl font-bold">{overview.activeDeliveryPersons}</p>
                    <p className="text-sm text-[hsl(var(--muted-foreground))] truncate">Delivery Team</p>
                  </div>
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardContent className="p-4">
                <div className="flex items-center gap-3">
                  <div className="w-12 h-12 bg-purple-100 rounded-xl flex items-center justify-center flex-shrink-0">
                    <Package className="w-6 h-6 text-purple-600" />
                  </div>
                  <div className="min-w-0">
                    <p className="text-2xl font-bold">
                      {inventory.inCirculationLarge + inventory.inCirculationSmall}
                    </p>
                    <p className="text-sm text-[hsl(var(--muted-foreground))] truncate">Bottles Out</p>
                  </div>
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardContent className="p-4">
                <div className="flex items-center gap-3">
                  <div className="w-12 h-12 bg-emerald-100 rounded-xl flex items-center justify-center flex-shrink-0">
                    <TrendingUp className="w-6 h-6 text-emerald-600" />
                  </div>
                  <div className="min-w-0">
                    <p className="text-2xl font-bold">{formatPaise(todayRevenue)}</p>
                    <p className="text-sm text-[hsl(var(--muted-foreground))] truncate">Today's Revenue</p>
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>
        </Section>

        {/* Two Column Section */}
        <Section spacing="sm">
          <div className="grid lg:grid-cols-2 gap-6">
            {/* Today's Deliveries */}
            <Card>
              <CardHeader>
                <CardTitle>Today's Deliveries</CardTitle>
              </CardHeader>
              <CardContent className="space-y-3">
                <div className="flex items-center justify-between p-3 bg-[hsl(var(--muted))] rounded-lg">
                  <span className="text-[hsl(var(--muted-foreground))]">Total Scheduled</span>
                  <span className="font-semibold">{todayStats.total}</span>
                </div>
                <div className="flex items-center justify-between p-3 bg-emerald-50 rounded-lg">
                  <div className="flex items-center gap-2">
                    <CheckCircle className="w-4 h-4 text-emerald-600" />
                    <span className="text-emerald-700">Delivered</span>
                  </div>
                  <span className="font-semibold text-emerald-700">{todayStats.delivered}</span>
                </div>
                <div className="flex items-center justify-between p-3 bg-amber-50 rounded-lg">
                  <div className="flex items-center gap-2">
                    <Clock className="w-4 h-4 text-amber-600" />
                    <span className="text-amber-700">Pending</span>
                  </div>
                  <span className="font-semibold text-amber-700">{todayStats.scheduled}</span>
                </div>
                <div className="flex items-center justify-between p-3 bg-red-50 rounded-lg">
                  <div className="flex items-center gap-2">
                    <AlertCircle className="w-4 h-4 text-red-600" />
                    <span className="text-red-700">Not Delivered / Paused</span>
                  </div>
                  <span className="font-semibold text-red-700">
                    {todayStats.notDelivered + todayStats.paused}
                  </span>
                </div>
              </CardContent>
            </Card>

            {/* Inventory Status */}
            <Card>
              <CardHeader>
                <CardTitle>Inventory Status</CardTitle>
              </CardHeader>
              <CardContent className="space-y-5">
                <div>
                  <div className="flex items-center justify-between mb-2">
                    <span className="text-sm text-[hsl(var(--muted-foreground))]">1L Bottles</span>
                    <span className="text-sm font-medium">
                      {inventory.availableLarge} available / {inventory.totalLarge} total
                    </span>
                  </div>
                  <div className="h-2 bg-[hsl(var(--muted))] rounded-full overflow-hidden">
                    <div 
                      className="h-full bg-[hsl(var(--primary))] rounded-full transition-all"
                      style={{ 
                        width: `${inventory.totalLarge > 0 
                          ? (inventory.inCirculationLarge / inventory.totalLarge) * 100 
                          : 0}%` 
                      }}
                    />
                  </div>
                  <p className="text-xs text-[hsl(var(--muted-foreground))] mt-1">
                    {inventory.inCirculationLarge} with customers
                  </p>
                </div>

                <div>
                  <div className="flex items-center justify-between mb-2">
                    <span className="text-sm text-[hsl(var(--muted-foreground))]">500ml Bottles</span>
                    <span className="text-sm font-medium">
                      {inventory.availableSmall} available / {inventory.totalSmall} total
                    </span>
                  </div>
                  <div className="h-2 bg-[hsl(var(--muted))] rounded-full overflow-hidden">
                    <div 
                      className="h-full bg-amber-500 rounded-full transition-all"
                      style={{ 
                        width: `${inventory.totalSmall > 0 
                          ? (inventory.inCirculationSmall / inventory.totalSmall) * 100 
                          : 0}%` 
                      }}
                    />
                  </div>
                  <p className="text-xs text-[hsl(var(--muted-foreground))] mt-1">
                    {inventory.inCirculationSmall} with customers
                  </p>
                </div>

                <Link 
                  href="/admin/inventory"
                  className="block text-center text-sm text-[hsl(var(--primary))] font-medium hover:underline"
                >
                  Manage Inventory â†’
                </Link>
              </CardContent>
            </Card>
          </div>
        </Section>

        {/* Recent Transactions */}
        <Section spacing="sm">
          <Card>
            <CardHeader>
              <CardTitle>Recent Transactions</CardTitle>
            </CardHeader>
            <CardContent>
              {recentTransactions.length === 0 ? (
                <div className="text-center py-8 text-[hsl(var(--muted-foreground))]">
                  No recent transactions
                </div>
              ) : (
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>Customer</TableHead>
                      <TableHead>Type</TableHead>
                      <TableHead>Amount</TableHead>
                      <TableHead className="hidden sm:table-cell">Date</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {recentTransactions.slice(0, 10).map((tx: any) => (
                      <TableRow key={tx.id}>
                        <TableCell className="font-medium">
                          {tx.wallet?.customer?.name || "Unknown"}
                        </TableCell>
                        <TableCell>
                          <Badge
                            variant={
                              tx.type === "WALLET_TOPUP" ? "success" :
                              tx.type === "PENALTY_CHARGE" ? "destructive" :
                              "secondary"
                            }
                          >
                            {tx.type.replace(/_/g, " ")}
                          </Badge>
                        </TableCell>
                        <TableCell className={tx.amountPaise > 0 ? "text-emerald-600" : "text-red-600"}>
                          {tx.amountPaise > 0 ? "+" : ""}{formatPaise(tx.amountPaise)}
                        </TableCell>
                        <TableCell className="hidden sm:table-cell text-[hsl(var(--muted-foreground))]">
                          {formatDateTime(tx.createdAt)}
                        </TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              )}
            </CardContent>
          </Card>
        </Section>
      </Container>
    </div>
  )
}
